import React from "react";
import { ZtimeRange } from "zerod";
// import { Row, Col } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZtimeRange/doc.md";
export default AmdDocHOC(doc, {
	demo1: function(tool) {
		class Myjavascript extends React.PureComponent {
			state = {
				times: [],
			};
			onChange = (times, timesString) => {
				console.log(times, timesString);
				this.setState({
					times,
				});
			};
			render() {
				return (
					<div style={{ width: "300px" }}>
						<ZtimeRange value={this.state.times} onChange={this.onChange} />
					</div>
				);
			}
		}
		return <Myjavascript />;
	},
});
